// The dropdown wrapper (`<div>`)
.dropup,
.dropend,
.dropdown,
.dropstart {
  position: relative;
}

.dropdown-toggle {
  white-space: nowrap;

  // Generate the caret automatically
  @include caret();
}

// The dropdown menu
.dropdown-menu {
  position: absolute;
  z-index: var(--#{$variable-prefix}zindex-dropdown, $zindex-dropdown);
  display: none; // none by default, but block on "open" of the menu
  min-width: var(--#{$variable-prefix}dropdown-min-width, $dropdown-min-width);
  padding: var(--#{$variable-prefix}dropdown-padding-y, $dropdown-padding-y) var(--#{$variable-prefix}dropdown-padding-x, $dropdown-padding-x);
  margin: 0; // Override default margin of ul
  @include font-size($dropdown-font-size);
  color: var(--#{$variable-prefix}dropdown-color, $dropdown-color);
  text-align: left; // Ensures proper alignment if parent has it changed (e.g., modal footer)
  list-style: none;
  background-color: var(--#{$variable-prefix}dropdown-bg, $dropdown-bg);
  background-clip: padding-box;
  border: $dropdown-border-width solid $dropdown-border-color;
  @include border-radius($dropdown-border-radius);
  @include box-shadow($dropdown-box-shadow);

  &[data-bs-popper] {
    top: 100%;
    left: 0;
    margin-top: var(--#{$variable-prefix}dropdown-spacer, $dropdown-spacer);
  }
}

// scss-docs-start responsive-breakpoints
// We deliberately hardcode the `bs-` prefix because we check
// this custom property in JS to determine Popper's positioning

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

    .dropdown-menu#{$infix}-start {
      --bs-position: start;

      &[data-bs-popper] {
        right: auto;
        left: 0;
      }
    }

    .dropdown-menu#{$infix}-end {
      --bs-position: end;

      &[data-bs-popper] {
        right: 0;
        left: auto;
      }
    }
  }
}
// scss-docs-end responsive-breakpoints

// Allow for dropdowns to go bottom up (aka, dropup-menu)
// Just add .dropup after the standard .dropdown class and you're set.
.dropup {
  .dropdown-menu[data-bs-popper] {
    top: auto;
    bottom: 100%;
    margin-top: 0;
    margin-bottom: var(--#{$variable-prefix}dropdown-spacer, $dropdown-spacer);
  }

  .dropdown-toggle {
    @include caret(up);
  }
}

.dropend {
  .dropdown-menu[data-bs-popper] {
    top: 0;
    right: auto;
    left: 100%;
    margin-top: 0;
    margin-left: var(--#{$variable-prefix}dropdown-spacer, $dropdown-spacer);
  }

  .dropdown-toggle {
    @include caret(end);
    &::after {
      vertical-align: 0;
    }
  }
}

.dropstart {
  .dropdown-menu[data-bs-popper] {
    top: 0;
    right: 100%;
    left: auto;
    margin-top: 0;
    margin-right: var(--#{$variable-prefix}dropdown-spacer, $dropdown-spacer);
  }

  .dropdown-toggle {
    @include caret(start);
    &::before {
      vertical-align: 0;
    }
  }
}


// Dividers (basically an `<hr>`) within the dropdown
.dropdown-divider {
  height: 0;
  margin: $dropdown-divider-margin-y 0;
  overflow: hidden;
  border-top: 1px solid $dropdown-divider-bg;
}

// Links, buttons, and more within the dropdown menu
//
// `<button>`-specific styles are denoted with `// For <button>s`
.dropdown-item {
  display: block;
  width: 100%; // For `<button>`s
  padding: var(--#{$variable-prefix}dropdown-item-padding-y, $dropdown-item-padding-y) var(--#{$variable-prefix}dropdown-item-padding-x, $dropdown-item-padding-x);
  clear: both;
  font-weight: var(--#{$variable-prefix}font-weight-normal, $font-weight-normal);
  color: var(--#{$variable-prefix}dropdown-link-color, $dropdown-link-color);
  text-align: inherit; // For `<button>`s
  text-decoration: if($link-decoration == none, null, none);
  white-space: nowrap; // prevent links from randomly breaking onto new lines
  background-color: transparent; // For `<button>`s
  border: 0; // For `<button>`s

  // Prevent dropdown overflow if there's no padding
  // See https://github.com/twbs/bootstrap/pull/27703
  @if $dropdown-padding-y == 0 {
    &:first-child {
      @include border-top-radius($dropdown-inner-border-radius);
    }

    &:last-child {
      @include border-bottom-radius($dropdown-inner-border-radius);
    }
  }

  &:hover,
  &:focus {
    color: var(--#{$variable-prefix}dropdown-link-hover-color, $dropdown-link-hover-color);
    text-decoration: if($link-hover-decoration == underline, none, null);
    @include gradient-bg($dropdown-link-hover-bg);
  }

  &.active,
  &:active {
    color: var(--#{$variable-prefix}dropdown-link-active-color, $dropdown-link-active-color);
    text-decoration: none;
    @include gradient-bg($dropdown-link-active-bg);
  }

  &.disabled,
  &:disabled {
    color: var(--#{$variable-prefix}dropdown-link-disabled-color, $dropdown-link-disabled-color);
    pointer-events: none;
    background-color: transparent;
    // Remove CSS gradients if they're enabled
    background-image: if($enable-gradients, none, null);
  }
}

.dropdown-menu.show {
  display: block;
}

// Dropdown section headers
.dropdown-header {
  display: block;
  padding: var(--#{$variable-prefix}dropdown-header-padding, $dropdown-header-padding);
  margin-bottom: 0; // for use with heading elements
  @include font-size($font-size-sm);
  color: var(--#{$variable-prefix}dropdown-header-color, $dropdown-header-color);
  white-space: nowrap; // as with > li > a
}

// Dropdown text
.dropdown-item-text {
  display: block;
  padding: var(--#{$variable-prefix}dropdown-item-padding-y, $dropdown-item-padding-y) var(--#{$variable-prefix}dropdown-item-padding-x, $dropdown-item-padding-x);
  color: var(--#{$variable-prefix}dropdown-link-color, $dropdown-link-color);
}

// Dark dropdowns
.dropdown-menu-dark {
  color: var(--#{$variable-prefix}dropdown-dark-color, $dropdown-dark-color);
  background-color: var(--#{$variable-prefix}dropdown-dark-bg, $dropdown-dark-bg);
  border-color: var(--#{$variable-prefix}dropdown-dark-border-color, $dropdown-dark-border-color);
  @include box-shadow($dropdown-dark-box-shadow);

  .dropdown-item {
    color: var(--#{$variable-prefix}dropdown-dark-link-color, $dropdown-dark-link-color);

    &:hover,
    &:focus {
      color: var(--#{$variable-prefix}dropdown-dark-link-hover-color, $dropdown-dark-link-hover-color);
      @include gradient-bg($dropdown-dark-link-hover-bg);
    }

    &.active,
    &:active {
      color: var(--#{$variable-prefix}dropdown-dark-link-active-color, $dropdown-dark-link-active-color);
      @include gradient-bg($dropdown-dark-link-active-bg);
    }

    &.disabled,
    &:disabled {
      color: var(--#{$variable-prefix}dropdown-dark-link-disabled-color, $dropdown-dark-link-disabled-color);
    }
  }

  .dropdown-divider {
    border-color: var(--#{$variable-prefix}dropdown-dark-divider-bg, $dropdown-dark-divider-bg);
  }

  .dropdown-item-text {
    color: var(--#{$variable-prefix}dropdown-dark-link-color, $dropdown-dark-link-color);
  }

  .dropdown-header {
    color: var(--#{$variable-prefix}dropdown-dark-header-color, $dropdown-dark-header-color);
  }
}
